import axios from 'axios';

const baseURL = 'http://inasync-dev.eu-west-2.elasticbeanstalk.com/api';

export const actions = {
    getInterests({
        commit
    }) {
        return new Promise((resolve, reject) => {
            axios({
                    url: baseURL + '/Auth/GetInterestsAsString',
                    method: 'GET'
                })
                .then(resp => {
                    // console.log(resp.data.data)
                    commit('setInterest', resp.data.data);
                    resolve(resp.data);
                })
                .catch(err => {
                    reject()
                });
        });
    },
    login({
        commit,
    }, user) {
        return new Promise((resolve, reject) => {
            commit('auth_request');
            axios({
                    url: baseURL + '/Auth/LoginEmail',
                    data: user,
                    method: 'POST'
                })
                .then(resp => {

                    console.log(resp.headers['set-auth']);
                    const token = resp.headers['set-auth'];
                    const user = resp.data.data;
                    localStorage.setItem('token', token);
                    localStorage.setItem('user', JSON.stringify(user));

                    axios.defaults.headers.common['Authorization'] = token;
                    commit('auth_success', token, user);

                    // dispatch('getUser');
                    resolve(resp.data);
                })
                .catch(err => {
                    commit('auth_error');
                    localStorage.removeItem('token');
                    if (err.response) {
                        if (err.response.status)
                            reject(err.response);
                    } else {
                        reject();

                    }
                });
        });
    },
    forgotPassword({
        commit
    }, user) {
        return new Promise((resolve, reject) => {
            commit('auth_request');
            console.log(user.email);
            var emailData = new FormData();
            emailData.append('email', user.email);
            let userEmail = emailData.get('email');

            axios({
                    url: baseURL + '/accounts/password-reset/',
                    data: userEmail,
                    method: 'POST'
                })
                .then(resp => {
                    const token = resp.data.token;
                    const user = resp.data.user;
                    localStorage.setItem('token', token);
                    axios.defaults.headers.common['Authorization'] = token;
                    commit('auth_success', token, user);
                    commit('loading', false);
                    resolve(resp);
                })
                .catch(err => {
                    commit('auth_error');
                    commit('loading', false);
                    localStorage.removeItem('token');
                    reject(err);
                });
        });
    },
    registerUser({
        commit,
        dispatch
    }, user) {
        return new Promise((resolve, reject) => {
            commit('auth_request');
            // console.log(user.full_name);

            var userData = new FormData();
            userData.append('full_name', user.full_name);
            userData.append('email', user.email);
            userData.append('password', user.password);
            userData.append('school', user.school);

            axios({
                    url: baseURL + '/accounts/register/',
                    data: userData,
                    method: 'POST'
                })
                .then(resp => {
                    const token = resp.data.data.token;
                    const user = resp.data.data.data;
                    localStorage.setItem('token', token);
                    dispatch('getUser');
                    axios.defaults.headers.common['Authorization'] = token;
                    commit('auth_success', token, user);
                    commit('loading', false);
                    resolve(resp);
                })
                .catch(err => {
                    commit('auth_error');
                    commit('loading', false);
                    localStorage.removeItem('token');
                    reject(err);
                });
        });
    },
    UpdateProfile({
        commit
    }, user) {
        return new Promise((resolve, reject) => {
            commit('loading', true);
            var userData = new FormData();
            userData.append('full_name', user.updateUser.full_name);
            userData.append('school', user.updateUser.school);
            userData.append('phone', user.updateUser.phone);
            userData.append('gender', user.updateUser.gender);
            userData.append('username', user.updateUser.username);
            userData.append('pf_img', user.updateUser.pf_img);

            let userToken = localStorage.getItem('token');

            axios({
                    url: baseURL + '/accounts/user/',
                    data: userData,
                    method: 'PUT',
                    headers: {
                        'Content-Type': 'application/json',
                        Authorization: 'Bearer ' + userToken
                    }
                })
                .then(resp => {
                    localStorage.setItem('user', JSON.stringify(resp.data.data));
                    localStorage.setItem(
                        'userSchool',
                        JSON.stringify(resp.data.data.school)
                    );

                    commit('loading', false);
                    resolve(resp);
                })
                .catch(err => {
                    //console.log(err);
                    commit('loading', false);
                    reject(err);
                });
        });
    },
    logout({
        commit
    }) {
        return new Promise((resolve, reject) => {
            commit('logout');
            localStorage.removeItem('token');
            localStorage.removeItem('user');

            delete axios.defaults.headers.common['Authorization'];
            resolve();
        });
    }
};